import React from "react";
import { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
// import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Pagination from '@material-ui/lab/Pagination';
const useStyles = makeStyles((theme) => ({
  root: {
    display:"flex",
    flexDirection:"column",
    alignItems:"center",
    justifyContent:"center",
    width: "100%",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  accordion:{
    width:"50%"
  }
}));
const AccordionCompoent = ({ datas }) => {
  const [page, setPage] = useState(1);
  const [filterData,setFilterData] = useState([])
  const fData = datas.filter((v,idx) =>  idx < page * 25 && idx >= (page-1)*25)
  
  const handlePageChange = (event, value) => {
    setPage(value);
  };
  const classes = useStyles();
  return (
    <div className={classes.root}>
       
      {fData.map((data, idx) => (
        <Accordion key={data.name} className={classes.accordion}>
          <AccordionSummary
            
            aria-controls={`panel1a-content`}
            id={`panel1a-header`}
           
          >
            <img src={data.flag} style={{width:"20px",margin:"5px"}}></img>
            <Typography className={classes.heading}>{data.name}</Typography>
          </AccordionSummary>
          
          <AccordionDetails >
            <Typography>
              <p>Alpha2Code:{data.alpha2Code}</p>
              <p>Alpha3Code:{data.alpha3Code}</p>
              <p>NativeName:{data.nativeName}</p>
              <p>NativeName:{data.nativeName}</p>
              {data.altSpellings.map(altS => (<p>
                AltSpellings:{altS}
              </p>))}
              <p>NativeName:{data.nativeName}</p>
              {data.callingCodes.map(cC => 
                (
                  <p>CallingCodes:{cC}</p>
                ) )}
            </Typography>
          </AccordionDetails>
        </Accordion>
      ))}
      <Pagination count={Math.ceil(datas.length / 25)} page={page} onChange={handlePageChange} />
    </div>
  );
};

export default AccordionCompoent;
